.heading-behind {
  width: 0rem;
  height: 5rem;
  // height: 5rem;
  text-transform: uppercase;
  font-family: $font-Blinker;
  font-size: 9em;
  font-weight: 700;
  @media screen and (max-width: $width-sm) {
    width: 0rem;
    height: 3rem;
    font-size: 4em;
    font-weight: 700;
  }
  @media screen and (min-width: $width-md) {
    font-size: 8em;
  }
}
