.heading {
  font-size: 7rem !important;
  color: $color-primary;
  font-weight: 700 !important;
  font-family: $font-Rubik;
  font-stretch: 500px;
  padding-bottom: 0.5em;
  @media screen and (max-width: $width-sm) {
    font-size: 2.5em !important;
    color: $color-primary;
    font-weight: 700 !important;
    font-family: $font-Rubik;
    font-stretch: 500px;
  }
}
// .heading:before {
//   content: "\2014 \00A0";
