@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/buttons.scss";
@import "../../Assets/scss/Padding.scss";
@import "../../Assets/scss/breakpoints.scss";

.education-section {
  background-color: $color-background-variant;
  .heading-behind {
    color: $color-background;
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background-variant;
  }
}
.education {
  .MuiGrid-grid-sm-6 {
    max-width: 47%;
  }
  @media screen and (max-width: $width-sm) {
    .MuiGrid-grid-sm-6 {
      max-width: 97.5%;
    }
  }
  .card {
    background-color: lighten($color-background, 5%);
    border-radius: 10px;
    padding: 5px;
    margin: 8px;
    font-family: $font-Rubik;

    .top {
      padding-top: 10px;
      padding-bottom: 10px;
      padding-left: 5px;
      border-bottom: 2px solid $color-background;
    }
    .degree {
      color: $color-primary;
      font-size: 1.3rem;
      @media screen and (max-width: $width-sm) {
        font-size: 1rem;
      }
    }
    .year {
      margin: 0.5em 0em;
      text-transform: uppercase;
      font-size: 0.8rem;
      color: $color-secondary;
      text-align: center;
      @media screen and (max-width: $width-sm) {
        font-size: 0.8rem;
      }
    }
    .school {
      color: $color-secondary;
      font-size: 1.1rem;
    }
  }

  .accomplishment {
    color: $color-on-background;
    font-family: $font-Rubik;
    font-size: 1.1rem;
    line-height: 1.5rem;
  }
  ul {
    padding-inline-start: 20px;
    margin-top: 0.5em;
    margin-bottom: 0;
    padding-left: 40px;
    li {
      padding-bottom: 0.4em;
    }
  }
}
